.result {
  font-size: 24px;
  font-weight: 700;
  text-align: right;
  width: 92%;
  border: none;
  background-color: #F8F8F8;
}

.first-row,
.second-row,
.third-row,
.fourth-row,
.conflict {
  display: inline-block;
  width: 99%;
}

.global {
  padding: 15px;
  font-weight: bold;
  font-size: 16px;
  border: none;
  border-radius: 3px;
  background-color: #F2F2F2;
  float: left;
  width: 30%;
  margin: 0 2px;
  /*border: 1px solid #000;*/
}

.global:hover {
  padding: 15px;
  font-weight: bold;
  font-size: 16px;
  border: none;
  border-radius: 3px;
  background-color: #1A162F;
  color: white;
  opacity: .9;
  float: left;
  width: 30%;
  margin: 0 2px;
  /*border: 1px solid #000;*/
}

.conflict .left,
.conflict .right {
  float: left;
  position: relative;
}

.left {
  width: 74%;
}

.right {
  width: 24%;
}

.big {
  padding: 15px;
  font-weight: bold;
  font-size: 16px;
  border: none;
  border-radius: 3px;
  background-color: #F2F2F2;
  float: left;
  width: 64.5%;
  margin: 0 2px;
}

.small {
  padding: 15px;
  font-weight: bold;
  font-size: 16px;
  border: none;
  border-radius: 3px;
  background-color: #F2F2F2;
  float: left;
  width: 30%;
  margin: 0 2px;
}

.plus {
  height: 105px;
  width: 100%;
}

.green {
  background-color: #81C784;
}

.red {
  background-color: #F50258
}

.grey {
  background-color: #F2F2F2;
}

.white-text {
  color: #fff;
}

.top-margin {
  margin-top: 4px;
}