body,
html {
    height: 100%;
    background-image: url(../assets/images/main.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
}

.mySection .bg {
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../assets/images/slider.png);
    opacity: .9;
    width: 100%;
    height: 100%;
}

.mySection {
    position: relative;
    z-index: 5;
    height: 100%;
    width: 100%;
    color: #000;
    padding: 200px 0;
}