tr {
    height: 50px;
}

td {
    text-align: center;
    color: #fff;
}

.table td {
    padding: 8px;
    font-size: 1vw;
}

.table tr {
    height: 1vw;
}

@media screen and (max-width: 768px) {
    .mt-5 {
        margin-top: 0px !important;
    }

    .table td {
        font-size: 3vw;
    }

    .table tr {
        height: 2vw;
    }
}